// CompanyFormModal.tsx
"use client";

import React, { useEffect, useState } from "react";
import Button from "@/components/ui/button/Button";
import Input from "@/components/form/input/InputField";
import Label from "@/components/form/Label";

export interface CompanyFormData {
  name: string;
  address: string;
  phone: string;
  logoFile: File | null;
}

interface Props {
  open: boolean;
  mode: "add" | "edit";
  initialData?: CompanyFormData;
  onClose: () => void;
  onSubmit: (data: CompanyFormData) => void;
}

const emptyForm: CompanyFormData = {
  name: "",
  address: "",
  phone: "",
  logoFile: null,
};

export default function CompanyFormModal({
  open,
  mode,
  initialData,
  onClose,
  onSubmit,
}: Props) {
  const [form, setForm] = useState<CompanyFormData>(emptyForm);

  useEffect(() => {
    setForm(initialData ? { ...initialData, logoFile: null } : emptyForm);
  }, [initialData, open]);

  if (!open) return null;

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50">
      <div
        className="w-full max-w-2xl rounded-xl bg-white dark:bg-gray-900 shadow-xl"
        onClick={(e) => e.stopPropagation()}
      >
        {/* HEADER */}
        <div className="border-b px-6 py-4 dark:border-gray-800">
          <h2 className="text-lg font-semibold text-gray-900 dark:text-white">
            {mode === "add" ? "Add Company" : "Edit Company"}
          </h2>
        </div>

        {/* BODY */}
        <div className="p-6">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            {/* Company Name */}
            <div>
              <Label>
                Company Name <span className="text-red-500">*</span>
              </Label>
              <Input
                value={form.name}
                onChange={(e) =>
                  setForm({ ...form, name: e.target.value })
                }
              />
            </div>

            {/* Phone */}
            <div>
              <Label>Phone</Label>
              <Input
                value={form.phone}
                onChange={(e) =>
                  setForm({ ...form, phone: e.target.value })
                }
              />
            </div>

            {/* Address (full width) */}
            <div className="md:col-span-2">
              <Label>Address</Label>
              <textarea
                className="w-full rounded-md border px-3 py-2 dark:bg-gray-800 dark:border-gray-700"
                rows={3}
                value={form.address}
                onChange={(e) =>
                  setForm({ ...form, address: e.target.value })
                }
              />
            </div>

            {/* Logo (full width) */}
            <div className="md:col-span-2">
              <Label>Company Logo</Label>
              <input
                type="file"
                accept="image/*"
                className="block w-full text-sm text-gray-600 dark:text-gray-300
                           file:mr-4 file:rounded-md file:border-0
                           file:bg-gray-100 file:px-4 file:py-2
                           file:text-sm file:font-medium
                           hover:file:bg-gray-200
                           dark:file:bg-gray-800 dark:hover:file:bg-gray-700"
                onChange={(e) =>
                  setForm({
                    ...form,
                    logoFile: e.target.files?.[0] || null,
                  })
                }
              />
            </div>
          </div>
        </div>

        {/* FOOTER */}
        <div className="flex justify-end gap-3 border-t px-6 py-4 dark:border-gray-800">
          <Button variant="outline" onClick={onClose}>
            Cancel
          </Button>
          <Button onClick={() => onSubmit(form)}>
            {mode === "add" ? "Create" : "Update"}
          </Button>
        </div>
      </div>
    </div>
  );
}
