"use client";

import React, { useEffect, useRef, useState } from "react";
import Button from "@/components/ui/button/Button";
import Input from "@/components/form/input/InputField";
import CompanyFormModal, { CompanyFormData } from "./CompanyFormModal";
import ConfirmDeleteModal from "./ConfirmDeleteModal";
import { CompanyService } from "@/services/company.service";
import toast from "react-hot-toast";

/* =========================
   TYPES (COMPANY ONLY)
========================= */
interface CompanyDto {
  id: string;
  name: string;
  address?: string;
  phone?: string;
  logoUrl?: string;
  isActive: boolean;
  createdBy?: string;
  createdAt?: string;
}

/* =========================
   ACTION MENU (UNCHANGED)
========================= */
function ActionMenu({
  openUp,
  onEdit,
  onDelete,
}: {
  openUp?: boolean;
  onEdit: () => void;
  onDelete: () => void;
}) {
  const [open, setOpen] = useState(false);
  const ref = useRef<HTMLDivElement>(null);

  useEffect(() => {
    function handleClickOutside(e: MouseEvent) {
      if (ref.current && !ref.current.contains(e.target as Node)) {
        setOpen(false);
      }
    }
    if (open) document.addEventListener("mousedown", handleClickOutside);
    return () =>
      document.removeEventListener("mousedown", handleClickOutside);
  }, [open]);

  return (
    <div ref={ref} className="relative">
      <button
        onClick={() => setOpen((p) => !p)}
        className="rounded-full p-2 hover:bg-gray-100 dark:hover:bg-gray-700"
      >
        ‚ãÆ
      </button>

      {open && (
        <div
          className={`absolute right-0 z-50 w-36 rounded-lg border bg-white shadow-lg
          dark:border-gray-700 dark:bg-gray-900
          ${openUp ? "bottom-full mb-2" : "top-full mt-2"}`}
        >
          <button
            onClick={() => {
              onEdit();
              setOpen(false);
            }}
            className="flex w-full gap-2 px-4 py-2 text-sm hover:bg-gray-50 dark:hover:bg-gray-800"
          >
            ‚úèÔ∏è Edit
          </button>

          <button
            onClick={() => {
              onDelete();
              setOpen(false);
            }}
            className="flex w-full gap-2 px-4 py-2 text-sm text-red-600 hover:bg-red-50 dark:hover:bg-red-900/20"
          >
            üóë Delete
          </button>
        </div>
      )}
    </div>
  );
}

/* =========================
   COMPANY TABLE
========================= */
export default function CompanyTable() {
  const [companies, setCompanies] = useState<CompanyDto[]>([]);
  const [search, setSearch] = useState("");

  const [openModal, setOpenModal] = useState(false);
  const [mode, setMode] = useState<"add" | "edit">("add");
  const [selectedCompany, setSelectedCompany] =
    useState<CompanyDto | null>(null);

  const [openDelete, setOpenDelete] = useState(false);
  const [deleting, setDeleting] = useState(false);
  const [loading, setLoading] = useState(false);

  /* =========================
     LOAD COMPANIES
  ========================= */
  async function loadCompanies() {
    setLoading(true);
    try {
      const res = await CompanyService.getAll();
      setCompanies(res.data.data);
    } catch {
      toast.error("Failed to load companies");
    } finally {
      setLoading(false);
    }
  }

  useEffect(() => {
    loadCompanies();
  }, []);

  /* =========================
     ADD / UPDATE
  ========================= */
  async function handleSubmit(data: CompanyFormData) {
    try {
      const formData = new FormData();
      formData.append("Name", data.name);
      formData.append("Phone", data.phone || "");
      formData.append("Address", data.address || "");

      if (data.logoFile) {
        formData.append("LogoFile", data.logoFile);
      }

      if (mode === "add") {
        await CompanyService.create(formData);
        toast.success("Company created successfully");
      } else {
        if (!selectedCompany?.id) {
          toast.error("Company ID missing");
          return;
        }
        await CompanyService.updateCompany(selectedCompany.id, formData);
        toast.success("Company updated successfully");
      }

      setOpenModal(false);
      loadCompanies();
    } catch (err: any) {
      toast.error(err?.response?.data?.message || "Operation failed");
    }
  }

  /* =========================
     DELETE
  ========================= */
  async function confirmDelete() {
    if (!selectedCompany) return;

    try {
      setDeleting(true);
      await CompanyService.delete(selectedCompany.id);
      toast.success("Company deleted successfully");
      setOpenDelete(false);
      loadCompanies();
    } catch {
      toast.error("Failed to delete company");
    } finally {
      setDeleting(false);
    }
  }

  /* =========================
     FILTER (FIXED)
  ========================= */
  const filteredCompanies = companies.filter((c) =>
    (c.name ?? "").toLowerCase().includes(search.toLowerCase())
  );

  return (
    <div className="w-full rounded-xl bg-white px-6 pb-6 dark:bg-gray-900">
      <h2 className="mb-4 text-xl font-semibold text-gray-800 dark:text-white">
        Companies
      </h2>

      {/* TOP BAR */}
      <div className="mb-4 flex items-center justify-between">
        <Input
          placeholder="Search companies..."
          value={search}
          onChange={(e) => setSearch(e.target.value)}
          className="w-64"
        />
        <Button
          size="sm"
          onClick={() => {
            setMode("add");
            setSelectedCompany(null);
            setOpenModal(true);
          }}
        >
          + Add Company
        </Button>
      </div>

      {/* TABLE */}
      <div className="overflow-x-auto">
        <table className="w-full border-collapse">
          <thead className="bg-gray-50 dark:bg-gray-800">
            <tr className="text-left text-sm text-gray-600 dark:text-gray-300">
              <th className="px-3 py-3">Company</th>
              <th className="px-3">Phone</th>
              <th className="px-3">Address</th>
              <th className="px-3">Logo</th>
              <th className="px-3 text-right">Action</th>
            </tr>
          </thead>

          <tbody>
            {loading ? (
              <tr>
                <td colSpan={5} className="py-6 text-center text-sm">
                  Loading companies...
                </td>
              </tr>
            ) : (
              filteredCompanies.map((c, i) => (
                <tr
                  key={c.id}
                  className="border-t hover:bg-gray-50 dark:hover:bg-gray-800 transition"
                >
                  <td className="px-3 py-4 font-medium">{c.name}</td>
                  <td className="px-3 text-sm">{c.phone || "-"}</td>
                  <td className="px-3 text-sm text-gray-600 dark:text-gray-400">
                    {c.address || "-"}
                  </td>
                  <td className="px-3">
                    {c.logoUrl ? (
                      <img
                        src={c.logoUrl}
                        className="h-8 object-contain"
                        alt={c.name}
                      />
                    ) : (
                      "-"
                    )}
                  </td>
                  <td className="px-3 text-right">
                    <ActionMenu
                      openUp={i === filteredCompanies.length - 1}
                      onEdit={() => {
                        setMode("edit");
                        setSelectedCompany(c);
                        setOpenModal(true);
                      }}
                      onDelete={() => {
                        setSelectedCompany(c);
                        setOpenDelete(true);
                      }}
                    />
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      </div>

      {/* MODALS */}
      <CompanyFormModal
        open={openModal}
        mode={mode}
        initialData={
          selectedCompany
            ? {
                name: selectedCompany.name,
                phone: selectedCompany.phone || "",
                address: selectedCompany.address || "",
                logoFile: null,
              }
            : undefined
        }
        onClose={() => setOpenModal(false)}
        onSubmit={handleSubmit}
      />

      <ConfirmDeleteModal
        open={openDelete}
        loading={deleting}
        onClose={() => setOpenDelete(false)}
        onConfirm={confirmDelete}
      />
    </div>
  );
}
